@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap);
body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:'Montserrat', sans-serif;background-color:#f5f7fa}


.QuestionPage{background-image:linear-gradient(160deg, #0f64f2 0%, #cbece8 100%)}.QuestionApp{height:100vh;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}h2{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;text-align:center}.go-back-arrow{text-decoration:none;display:inline-block;content:'';height:10px;width:10px;margin-right:12px;border-bottom:3px solid #fff;border-right:3px solid #fff;-webkit-transform:rotate(135deg);transform:rotate(135deg)}.question-title{color:#fff;margin-top:0;padding-left:1em;padding-top:1em}.video-container{height:85%;max-width:85%;-webkit-flex-basis:85%;flex-basis:85%}.options-container{padding:1em}.selection-option{padding:0.5em;width:50%;text-overflow:wrap;font-weight:500;font-size:1em;text-align:right}.selection-option:hover{cursor:pointer;background-color:#a1ffa1}.question-selection-menu{padding:1em}.question-tab-panel{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap}.question-button{padding:1em;-webkit-flex-basis:33%;flex-basis:33%}@media screen and (max-width: 480px){.question-button{-webkit-flex-basis:50%;flex-basis:50%}}@media screen and (max-width: 400px){.question-button{-webkit-flex-basis:100%;flex-basis:100%}}.question-button:hover{cursor:pointer;background-color:#a1ffa1}

h1{font-size:2em}.hero-row{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;background-color:#0f64f2;background-image:linear-gradient(160deg, #0f64f2 0%, #cbece8 100%);height:100vh;-webkit-justify-content:center;justify-content:center;font-weight:100}.hero-keyword{color:#0d61f2}.hero-row-left{padding:1em;margin:1em;background:rgba(255,255,255,0.35);box-shadow:0 8px 32px 0 rgba(31,38,135,0.37);backdrop-filter:blur(11.5px);-webkit-backdrop-filter:blur(11.5px);border-radius:10px;border:1px solid rgba(255,255,255,0.18);width:50vws}.hero-image{width:50vw;min-width:200px}.tryit{width:100%}.tryit:hover{cursor:pointer}@media screen and (max-width: 600px){.hero-image{width:100%}.hero-row-left{padding-left:0;text-align:center}}

.howworks-card{height:250px;width:250px;border:solid 1px #0d61f2;border-radius:1em;padding:1em;background-color:white}.howworks-img{height:150px}

.home-howworks{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;grid-column-gap:1em;-webkit-column-gap:1em;column-gap:1em;grid-row-gap:1em;row-gap:1em;-webkit-justify-content:center;justify-content:center}

